/* src/App.css */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.App-header {
  background-color: #000000;
  padding: 0 20px;
}

.App-header nav ul {
  list-style-type: none;
  padding: 0;
}

.App-header nav ul li {
  display: inline;
  margin: 0 10px;
}

.App-header nav ul li a {
  color: white;
  text-decoration: none;
}

.App-main {
  padding: 20px;
}

.App-footer {
  background-color: #f5f5f7;
  color: #1d1d1f;
  padding: 20px;
  font-size: 12px;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  max-width: 980px;
  margin: 0 auto;
}

.footer-column {
  flex: 1;
  margin-right: 20px;
}

.footer-column h3 {
  font-size: 12px;
  margin-bottom: 10px;
}

.footer-column ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.footer-column ul li {
  margin-bottom: 8px;
}

.footer-column ul li a {
  color: #424245;
  text-decoration: none;
}

.footer-column ul li a:hover {
  text-decoration: underline;
}

.footer-notes {
  max-width: 980px;
  margin: 20px auto;
  padding-top: 10px;
  border-top: 1px solid #d2d2d7;
}

.footer-legal {
  max-width: 980px;
  margin: 0 auto;
  padding-top: 10px;
  border-top: 1px solid #d2d2d7;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer-legal ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.footer-legal ul li {
  margin-right: 10px;
}

.footer-legal ul li:not(:last-child)::after {
  content: "|";
  margin-left: 10px;
}

.footer-legal p {
  margin: 5px 0;
}

.copyright {
  border-top: 1px solid #444;
  padding-top: 10px;
}

/* 在文件末尾添加以下样式 */

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1024px;
  margin: 0 auto;
  height: 44px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: auto;
  height: auto;
}

/* 添加媒体查询以在小屏幕上进一步调整大小 */
@media (max-width: 768px) {
  .logo img {
    max-height: 24px; /* 在小屏幕上稍微减小 logo 大小 */
  }
}

.main-nav {
  display: flex;
  flex-grow: 1; /* 让导航占据剩余空间 */
  justify-content: center; /* 居中导航项 */
}

.nav-item {
  color: #f5f5f7;
  font-size: 12px;
  margin: 0 10px;
  cursor: pointer;
}

.search, .shopping-bag {
  color: #f5f5f7;
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  top: 44px;
  left: 0;
  right: 0;
  background-color: #000000;
  padding: 20px 0;
}

.dropdown-content {
  display: flex;
  justify-content: space-between;
  max-width: 1024px;
  margin: 0 auto;
}

.dropdown-column {
  flex: 1;
  margin-right: 20px;
}

.dropdown-column h3 {
  color: #f5f5f7;
  font-size: 12px;
  margin-bottom: 10px;
}

.dropdown-column ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.dropdown-column ul li {
  color: #a1a1a6;
  font-size: 12px;
  margin-bottom: 8px;
  cursor: pointer;
}

.dropdown {
  background-color: transparent;
  color: white;
  padding: 20px;
  margin-right: 20px;
}

.dropdown ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.dropdown li {
  padding: 5px 0;
}

.dropdown input {
  margin-bottom: 10px;
  background-color: white;
  border: none;
  padding: 5px;
}

.dropdown button {
  margin-top: 10px;
  background-color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.video-carousel {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.video-container {
  display: flex;
  transition: transform 0.3s ease;
}

.video-container video {
  margin-right: 10px;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  z-index: 2;
}

.carousel-button.prev {
  left: 0;
}

.carousel-button.next {
  right: 0;
}

.App-header.light-mode {
  background-color: #ffffff;
}

.App-header.dark-mode {
  background-color: #282c34;
}